export default {
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    width: {
      type: Number,
      default: 500,
    },
  },
  emits: ['close', 'show-content'],
  data() {
    return {
      showOverlay: false,
      showContent: false,
    };
  },
  watch: {
    show: {
      immediate: true,
      handler(show) {
        this.$nextTick(() => {
          if (show) {
            this.showOverlay = true;
            document.body.classList.add('no-scroll');
          } else {
            this.showContent = false;
            document.body.classList.remove('no-scroll');
          }
        });
      },
    },
    showContent(showContent) {
      if (showContent) {
        this.$emit('show-content');
      }
    },
  },
  methods: {
    close() {
      this.showContent = false;
      document.body.classList.remove('no-scroll');
    },
  },
};
