import { mapGetters, mapActions } from 'vuex';

import TopicView from '../components/TopicView.vue';

export default {
  components: {
    TopicView,
  },
  data() {
    return {
      loading: false,
    };
  },
  computed: { },
  methods: {
    ...mapActions([
      'topics/deleteTopic',
    ]),
    async onDeleteTopic() {
      this.loading = true;
      await this.deleteTopic({ topicId: this.topic.id });
      this.$router.push({
        name: 'Topics',
      });
    },
  },
  mounted(){
        console.log( 'topicpage', this.replyes );

  }
};
