import BaseModal from './BaseModal.vue';
import BaseButton from './BaseButton.vue';

export default {
  components: {
    BaseModal,
    BaseButton,
  },
  props: {
    confirmButtonTitle: {
      type: String,
      default: 'Confirm',
    },
    dismissButtonTitle: {
      type: String,
      default: 'Dismiss',
    },
  },
  emits: ['dismiss', 'confirm'],
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    this.show = true;
  },
  methods: {
    confirm() {
      this.$emit('confirm');
      this.show = false;
    },
  },
};
